"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Search, Mail, Calendar, ShoppingBag, DollarSign } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent } from "@/components/ui/card"
import { customersAPI, getCurrentUser, authAPI, Customer } from "@/lib/api"
import { useToast } from "@/hooks/use-toast"

export default function AdminCustomers() {
  const router = useRouter()
  const { toast } = useToast()
  const [customers, setCustomers] = useState<Customer[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check authentication
    const user = getCurrentUser()
    if (!user || !user.is_admin) {
      router.push("/login")
      return
    }

    fetchCustomers()
  }, [router])

  const fetchCustomers = async () => {
    try {
      const data = await customersAPI.getAll()
      setCustomers(data)
    } catch (error: any) {
      if (error.message.includes("401") || error.message.includes("403")) {
        authAPI.logout()
        router.push("/login")
      } else {
        toast({
          title: "Error",
          description: "Failed to load customers",
          variant: "destructive",
        })
      }
    } finally {
      setIsLoading(false)
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString("en-US", { year: "numeric", month: "short", day: "numeric" })
  }

  const filteredCustomers = customers.filter(
    (customer) =>
      customer.first_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      customer.last_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      customer.email.toLowerCase().includes(searchQuery.toLowerCase())
  )

  if (isLoading) {
    return (
      <div className="p-8 flex items-center justify-center min-h-screen">
        <p className="text-muted-foreground">Loading...</p>
      </div>
    )
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold">Customers</h1>
        <p className="text-muted-foreground" style={{ fontFamily: "var(--font-inter)" }}>
          View and manage your customer base
        </p>
      </div>

      {/* Search */}
      <div className="flex gap-4 mb-6">
        <div className="relative flex-1 max-w-md">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search customers..."
            className="pl-10"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
      </div>

      {/* Customers Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredCustomers.length === 0 ? (
          <div className="col-span-full py-12 text-center text-muted-foreground">
            No customers found
          </div>
        ) : (
          filteredCustomers.map((customer) => (
            <Card key={customer.id} className="bg-white border-[#7BA4D0]/30">
              <CardContent className="p-6">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-[#0D2440]">
                      {customer.first_name} {customer.last_name}
                    </h3>
                    <div className="flex items-center gap-2 mt-1 text-sm text-[#2E5E99]">
                      <Mail className="h-4 w-4" />
                      <span>{customer.email}</span>
                    </div>
                  </div>
                </div>

                <div className="space-y-3">
                  <div className="flex items-center gap-2 text-sm text-[#2E5E99]">
                    <Calendar className="h-4 w-4" />
                    <span style={{ fontFamily: "var(--font-inter)" }}>
                      Joined: {formatDate(customer.created_at)}
                    </span>
                  </div>

                  <div className="flex items-center justify-between pt-3 border-t border-[#7BA4D0]/20">
                    <div className="flex items-center gap-2">
                      <ShoppingBag className="h-4 w-4 text-[#2E5E99]" />
                      <div>
                        <p className="text-xs text-[#2E5E99]" style={{ fontFamily: "var(--font-inter)" }}>
                          Orders
                        </p>
                        <p className="text-sm font-semibold text-[#0D2440]">{customer.total_orders}</p>
                      </div>
                    </div>

                    <div className="flex items-center gap-2">
                      <DollarSign className="h-4 w-4 text-[#2E5E99]" />
                      <div>
                        <p className="text-xs text-[#2E5E99]" style={{ fontFamily: "var(--font-inter)" }}>
                          Total Spent
                        </p>
                        <p className="text-sm font-semibold text-[#0D2440]">
                          ${customer.total_spent.toFixed(2)}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>

      {/* Summary Stats */}
      {customers.length > 0 && (
        <Card className="mt-8 bg-white border-[#7BA4D0]/30">
          <CardContent className="p-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div>
                <p className="text-sm text-[#2E5E99]" style={{ fontFamily: "var(--font-inter)" }}>
                  Total Customers
                </p>
                <p className="text-2xl font-bold text-[#0D2440]">{customers.length}</p>
              </div>
              <div>
                <p className="text-sm text-[#2E5E99]" style={{ fontFamily: "var(--font-inter)" }}>
                  Total Orders
                </p>
                <p className="text-2xl font-bold text-[#0D2440]">
                  {customers.reduce((sum, c) => sum + c.total_orders, 0)}
                </p>
              </div>
              <div>
                <p className="text-sm text-[#2E5E99]" style={{ fontFamily: "var(--font-inter)" }}>
                  Total Revenue
                </p>
                <p className="text-2xl font-bold text-[#0D2440]">
                  ${customers.reduce((sum, c) => sum + c.total_spent, 0).toFixed(2)}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}

